<template>
  <div v-if="song" :class="openPage ? '' : 'PlayPage'">
    <div class="songPage" :class="openPage ? '' : 'close'">
      <!-- 背景 与遮罩 -->
      <div
        class="substitute"
        :style="`background-image:url(${
          song?.picUrl || song?.al.picUrl
        }?imageView=1&type=webp&thumbnail=200x0)`"
      >
        <div class="mark"></div>
      </div>
      <!-- 头部 -->
      <div class="songPage-top">
        <i
          @click.stop="
            $emit('open-Page');
            playBody();
            lyricsShow = false;
          "
          class="fa fa-angle-down"
          aria-hidden="true"
        ></i>
        <div class="song-cont">
          <div class="songName" style="text-align: center">{{ song.name }}</div>
          <!-- class="songAirst" -->
          <div class="songAirstBox" style="display: flex">
            <div>
              {{ artistAll(song?.song?.album?.artists ?? song?.ar) }}
            </div>
          </div>
        </div>
        <div class="songShare">
          <img class="iconImg" src="../assets/songShare-bai-48.svg" alt="" />
        </div>
      </div>
      <!-- 中间转盘 -->
      <div class="centerImg" v-if="!lyricsShow" @click="lyricsShow = true">
        <img
          class="zhizhen"
          :class="playMusic ? '' : 'playNull'"
          src="../assets/needle.png"
          alt=""
        />
        <img
          class="zhuanpan"
          :class="playMusic ? 'imgRotute' : ''"
          :src="`${
            song?.picUrl || song?.al.picUrl
          }?imageView=1&type=webp&thumbnail=200x0`"
          alt=""
        />
      </div>
      <LyricsView
        v-if="lyricsShow"
        @click.native="lyricsShow = false"
        :lyricsShow="lyricsShow"
        :updateTime="updateTime"
        :duration="duration"
        :song="song"
      />
      <!-- 下方控制列表 -->
      <div class="songPageFooter">
        <ul class="Miscellaneous">
          <li>
            <img src="../assets/songLove-bai-48.svg" alt="" />
          </li>
          <li><img src="../assets/songXiaZai-bai-48.svg" alt="" /></li>
          <li><img src="../assets/songSing-bai-48.svg" alt="" /></li>
          <li><img src="../assets/songPingLun-bai-48.svg" alt="" /></li>
          <!-- <li><img src="../assets/song" alt=""></li> -->
        </ul>
        <!-- 进度条 -->
        <div class="progressBar">
          <div class="nowTime">{{ songTimes(updateTime) }}</div>
          <div class="rangeBar">
            <input
              type="range"
              name=""
              id=""
              ref="input"
              min="0"
              v-model="value"
              @input="draging = true"
              :max="duration"
              @change="
                draging = false;
                $emit('upDate-time', $event.target.value);
              "
            />
            <div class="mark">
              <div class="markBox" :style="`width:${(value / duration) * 100}%`"></div>
            </div>
            <div
              class="stars"
              :class="playMusic ? 'move' : ''"
              :style="`left:calc(${(value / duration) * 100}% - 5rem)`"
            ></div>
          </div>
          <div class="endTime">{{ songTimes(duration) }}</div>
        </div>
        <!-- 控制栏 -->
        <div class="controls">
          <div class="loop" @click="$emit('toggle-Status', nowStatus)">
            <img
              class="iconImg"
              v-show="nowStatus == '循环列表'"
              src="../assets/songAllLoop-bai-48.svg"
              alt=""
            />
            <img
              class="iconImg"
              v-show="nowStatus == '单曲循环'"
              src="../assets/songoldLoop-bai-48.svg"
              alt=""
            />
            <img
              class="iconImg"
              v-show="nowStatus == '随机播放'"
              src="../assets/songRandom-bai-48.svg"
              alt=""
            />
          </div>
          <div class="songstutas">
            <div class="up" @click.stop="$emit('change-song', 'perv')">
              <img class="iconImg" src="../assets/songUp-bai-48.svg" alt="" />
            </div>
            <div class="play" v-show="!playMusic" @click.stop="$emit('Update-play')">
              <img src="../assets/songPlay-bai-48.svg" alt="" />
            </div>
            <div class="play" v-show="playMusic" @click.stop="$emit('Update-stop')">
              <img src="../assets/songPause-bai-48.svg" alt="" />
            </div>
            <div class="down" @click.stop="$emit('change-song', 'next')">
              <img class="iconImg" src="../assets/songDown-bai-48.svg" alt="" />
            </div>
          </div>
          <div class="songList" @click.stop="$emit('show-List')">
            <img class="iconImg" src="../assets/songList-bai-48.svg" alt="" />
          </div>
        </div>
      </div>
      <transition name="showS">
        <div class="showStatus" v-show="showStatus">
          {{ nowStatus }}
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
import LyricsView from "@/components/lyricsView.vue";
var _ = require("lodash");
export default {
  components: {
    LyricsView,
  },
  props: {
    song: Object,
    playMusic: Boolean,
    openPage: Boolean,
    updateTime: [Number, String],
    duration: [Number, String],
    loopStatus: Array,
    nowStatus: String,
  },
  data: function () {
    return {
      lyricsShow: false,
      lastTime: 0,
      draging: false,
      tmpCurrentTime: this.updateTime,
      showStatus: false,
      value: this.updateTime,
    };
  },
  methods: {
    songTimes(time) {
      let min = Math.floor(time / 60);
      let sed = Math.floor(time % 60);
      return `${min < 10 ? `0${min}` : min}:${sed < 10 ? `0${sed}` : sed}`;
    },
    changeStatus: function () {
      this.showStatus = false;
    },
    playBody: function () {
      document.querySelector("body").style.overflow = "auto";
    },
    artistAll: function (arr) {
      let arrName = [];
      for (let i = 0; i < arr.length; i++) {
        arrName.push(arr[i].name);
      }
      return arrName.join(" / ");
    },
  },
  created() {
    this.debounceWhileSearch = _.debounce(this.changeStatus, 800);
  },
  watch: {
    updateTime: function () {
      if (!this.draging) {
        this.value = this.updateTime;
        // console.log("false")
      }
      // else{ 
      // console.log("true")
      // }
    },
    nowStatus: function () {
      // console.log("nowStatus");
      this.showStatus = true;
      this.debounceWhileSearch();
    },
  },
};
</script>

<style lang="less" scoped>
//showS
.showS-enter-active,
.showS-leave-active {
  transition: opacity 0.5s;
}
.showS-enter,
.showS-leave-to {
  opacity: 0;
}

.flex() {
  display: flex;
  align-items: center;
}
.tranAll() {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 12;
}
.iconImg {
  width: 30rem;
}

.PlayPage {
  overflow: hidden;
  .songPage {
    &.close {
      top: 100%;
    }
  }
}
.songPage {
  position: fixed;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.588);
  z-index: 299;
  transition: all 0.5s;
  color: #fff;
  overflow: hidden;
  .showStatus {
    position: absolute;
    top: 70%;
    left: 50%;
    transform: translateX(-50%);
    background-color: rgba(255, 255, 255, 0.531);
    color: #000;
    border-radius: 50rem;
    padding: 10rem 25rem;
    z-index: 999;
  }
  .substitute {
    background-position: center;
    background-size: cover;
    position: absolute;
    filter: blur(10rem) brightness(0.5);
    transform: scale(1.1);
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 0;
    .mark {
      position: absolute;
      .tranAll();
      width: 100%;
      height: 100vh;
      background-color: rgba(0, 0, 0, 0.504);
      z-index: 0;
    }
  }
  .songPage-top {
    position: relative;
    z-index: 12;
    padding: 20rem 15rem;
    .flex();
    justify-content: space-between;
    .song-cont {
      width: 100%;
      overflow: hidden;
      .songName {
        text-align: center;
      }
      .songAirstBox {
        display: flex;
        > div {
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow: hidden;
          margin: 0 auto;
        }
      }
    }
    i {
      color: #fff;
      font-size: 30rem;
      z-index: 999;
    }
  }
  @keyframes songImgRotute {
    from {
      transform: translate(-50%, -50%) rotate(0);
    }
    to {
      transform: translate(-50%, -50%) rotate(360deg);
    }
  }
  .centerImg {
    position: relative;
    z-index: 12;
    min-height: 350rem;
    position: relative;
    top: 25rem;
    animation-delay: 1s;
    display: flex;
    justify-content: center;
    align-items: self-start;
    &::after {
      content: "";
      border-radius: 50%;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) scale(1.05);
      width: 250rem;
      height: 250rem;
      background-color: rgba(255, 255, 255, 0.307);
      position: absolute;
    }
    .zhizhen {
      position: relative;
      width: 84rem;
      z-index: 13;
      top: -20rem;
      left: 0;
      transform: translateX(50%);
      transition: all 0.3s;
      transform: rotate(348deg) translateX(25%);
      &.playNull {
        transform: rotate(326deg) translateX(25%);
      }
      transform-origin: 33rem 10rem;
    }
    .zhuanpan {
      animation: songImgRotute 8s linear infinite;
      animation-play-state: paused;
      border: 40rem solid #000;
      .tranAll();
      width: 250rem;
      height: 250rem;
      border-radius: 50%;
      &.imgRotute {
        animation: songImgRotute 8s linear infinite;
        animation-play-state: running;
      }
    }
  }
  .songPageFooter {
    position: absolute;
    z-index: 12;
    bottom: 0;
    width: 100%;
    padding: 15rem 5rem 5rem;
    .Miscellaneous {
      padding: 20rem 15rem;
      // margin-top: 85rem;
      .flex();
      justify-content: center;
      li {
        width: 25rem;
        margin: 0 20rem;
      }
    }
    .progressBar {
      width: 100%;
      .flex();
      padding: 10rem 5rem;
      justify-content: center;
      align-items: center;
      .nowTime {
        flex-shrink: 0;
        margin-right: 5rem;
      }
      .rangeBar {
        width: 100%;
        position: relative;
        display: flex;
        input {
          width: 100%;
          opacity: 0;
          z-index: 99;
          position: relative;
        }
        .mark {
          .tranAll();
          width: 100%;
          height: 1rem;
          background-color: rgb(149, 149, 149);
          border-radius: 50rem;
          position: absolute;
          z-index: 0;
          .markBox {
            position: absolute;
            left: 0;
            width: 0%;
            height: calc(100%);
            background-color: rgb(45, 45, 45);
            border-radius: 50rem;
            position: absolute;
            z-index: 1;
            // &::after {
            //   .tranAll();
            //   content: "";
            //   width: 5rem;
            //   height: 5rem;
            //   border-radius: 50%;
            //   background-color: #fff;
            // }
          }
        }
        @keyframes flicker {
          0% {
            background-color: rgb(255, 255, 255);
          }
          50% {
            background-color: rgb(0, 0, 0);
          }
          100% {
            background-color: rgb(255, 255, 255);
          }
        }
        .stars {
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          left: 0;
          width: 8rem;
          height: 8rem;
          border-radius: 50%;
          background-color: #fff;
          &.move {
            animation: flicker 2s linear infinite reverse;
          }
        }
      }
      .endTime {
        margin-left: 5rem;
        flex-shrink: 0;
      }
    }
    .controls {
      padding: 15rem;
      .flex();
      justify-content: space-between;
      .loop {
        width: 30rem;
        flex-shrink: 0;
      }
      .songstutas {
        .flex();
        > div {
          margin: 0 5rem;
        }
        .up {
        }
        .play {
        }
        .down {
        }
      }
      .songList {
        width: 30rem;
        flex-shrink: 0;
      }
    }
  }
}
</style>
