<template>
  <div>
    <div class="lyrics">
      <ul class="songLyrics" ref="ul">
        <div v-if="wait" class="wait">歌词正在填充中...</div>
        <!-- v-else -->
        <li
        v-else
          class="item"
          v-for="(item, index) in lryEvery"
          :class="{ showItem: currentIndex === index }"
          :key="index"
        >
          <span :style="`animation-duration:${nextTime}s`">
            {{ item.text || "..." }}
          </span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    lyricsShow: Boolean,
    duration: [Number, String],
    updateTime: [Number, String],
    song: Object,
  },
  data: function () {
    return {
      lyric: [],
      times: "",
      songLyric: [],
      wait: true,
      lry: null,
      El: null,
      ulDomH: null,
      currentIndex: 0,
    };
  },
  computed: {
    lryEvery() {
      if (this.lry) {
        let tmp;
        tmp = this.lry
          .trim()
          .split("\n")
          .map((o) => {
            return o.split("]").map((t, i) => {
              if (i === 0) {
                let num = 0;
                t.slice(1)
                  .split(":")
                  .forEach((a, index) => {
                    if (index === 0) {
                      num += Number(a) * 60;
                    } else {
                      num += Number(a);
                    }
                  });
                return Number(num);
              } else {
                return t.trim();
              }
            });
          });

        tmp = tmp.map((o) => ({ time: o[0], text: o[1] }));
        return tmp;
      } else {
        return [];
      }
    },
    nextTime() {
      return (
        this.lryEvery[
          this.currentIndex + 1 === this.lryEvery.length
            ? this.lryEvery.length
            : this.currentIndex + 1
        ].time - this.lryEvery[this.currentIndex].time
      );
    },
  },
  mounted() {
    this.ulDomH = this.$refs.ul.offsetHeight / 2;
  },
  created() {
    this.axios("https://apis.netstart.cn/music/lyric", {
      params: {
        id: this.song?.id,
      },
    }).then((res) => {
      this.wait = false;
      this.lry = res.data.lrc.lyric;
    });
  },
  watch: {
    //ok
    song: function (res) {
      this.wait = true;
      this.songLyric = [];
      this.axios("https://apis.netstart.cn/music/lyric", {
        params: {
          id: res?.id,
        },
      }).then((res) => {
        this.lry = res.data.lrc.lyric;
        this.wait = false;
      });
    },
    updateTime: function (n) {
      // var o = this.findLyric()
      // console.log(o);
      this.lryEvery.forEach((o, i) => {
        if (n >= o.time) {
          this.currentIndex = i;
        }
      });
      if (this.lryEvery.length) {
        this.$refs.ul.style.transform = `translateY(-${
          35 * this.currentIndex - this.ulDomH
        }rem)`;
      }
    },
  },
};
</script>
<style lang="less" scoped>
@keyframes scan {
  0% {
    background-size: 0% 100%;
  }
  100% {
    background-size: 100% 100%;
  }
}
.lyrics {
  position: relative;
  z-index: 12;
  font-size: 15rem;
  text-shadow: 0 1rem 0 rgba(0, 0, 0, 0.607);
  display: flex;
  align-items: center;
  justify-content: center;
  overflow-y: auto;
  overflow-x: hidden;
  transition: all 0.3s;
  .songLyrics {
    max-height: 375rem;
    min-height: 375rem;
    transition: all 0.3s;
    text-align: center;
    .wait {
      line-height: 375rem;
    }

    .item {
      min-height: 35rem;
      span {
        font-size: 13rem;
        background: hsla(0, 0%, 100%, 0.339) -webkit-linear-gradient(
            left,
            rgb(255, 255, 255),
            #fff
          ) no-repeat 0 0;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-size: 0 100%;
        white-space: nowrap;
        line-height: 35rem;
      }
      &.showItem {
        span {
          background-size: 100% 100%;
          animation: scan ease-out;
          animation-delay: -0.35s;
        }
      }
    }
  }
}
</style>
